@import "../../../css-config/mixins.scss";

.hero-banner{
    padding: 40px 0;
    position: relative;
    background: #daede8;
    overflow-x: hidden;
    @include md{
       height: calc(100vh - 80px);
    }
    .content{
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column-reverse;
        max-width: calc(100% - 20px);
        margin: 0 auto;
        position: relative;
        @include md{
            flex-direction: row;
            max-width: 1200px;
        }
        .banner-img{
            position: relative;
            max-height: 25rem;
            top: -2rem;
            left: -1rem;
            max-width: 350px;
            height: 520px;
            margin-bottom: 20px;
            @include md{
                // z-index: 99;
                max-width:1600px;
                margin-right: 60px;
                margin-top: 50px;
                margin-bottom: -13px;
            }
            @include xxl{
                width: 600px;
                margin-right: 0px;
                margin-top: 0px;
            }
        }
        .text-content{
            color: #2b2e3d;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            @include md{
                position: absolute;
                left: 50px;
                top: 50%;
                transform: translateY(-50%);
            }
            @include xxl{
                left: 0;
            }
            h2{
                font-size: 50px;
                font-weight: 700;
                line-height: 1;
                margin-bottom: 20px;
                @include md{
                    font-size: 130px;
                }
                span{
                    color: #43cea7;
                }
            }
            p{
                max-width: 300px;
                font-size: 14px;
                line-height: 20px;
                margin-bottom: 20px;
                @include md{
                    max-width: 500px;
                    font-size: 18px;
                    line-height: 24px;
                    margin-bottom: 40px;
                }
              
            }
            .ctas{
                display: flex;
                justify-content: center;
                gap: 20px;
                .banner-cta{
                    text-transform: uppercase;
                    font-size: 13px;
                    font-weight: 500;
                   border: 2px solid #43cea7;
                   padding: 10px 20px;
                   cursor: pointer;
                   &.v2{
                    background-color: #43cea7;
                    color: black;
                   }
                   &:hover{
                    opacity: 0.6;
                   }
                }
            }
        }
    }
}
