
.Related-item{
    white-space: nowrap;
    position: relative;
    top: 20rem;
}
    .r-heading{
        margin-left: 5.5rem;
        margin-top: -8rem;
    }
    .Re-h1 {
        font-size: 34px;
        position: relative;
        top: 9rem;
        left: -1rem;
    }
    .Re-h1 span{
        font-size: 34px;
        position: relative;
        left: 0.1rem; 
        top: -0.1rem;
    }
    .line{
        width: 17rem;
        padding: 1px 0;
        position: relative;
        top: -5rem;
        left: -26.5rem;
        background-color: #43cea7;
    }


.R-container{
    margin-top: 4rem;
      display: flex;
      flex-direction: row;
      gap: 2rem;
}
    .r-main-card{
        background-color: #daede8;
        border: 2px solid #43cea7;
        border-radius: 10px;
        width: 20rem;
        height: 25rem;
        position: relative;
        top: 6rem;
        left: 5rem;
    }
        .h-main-card{
            margin-top: 1rem;
           margin-left: 2rem;
           font-size: 21px;
        }
        .web-card{
            margin-left: 3rem;
            margin-top: 2rem;
        }
         li{
            cursor: pointer;
            padding: 6px 0;
            display: flex; 

        }
        li:hover{
           color: #43cea7;
        }
    



@media (max-width: 768px) {
    .R-container {
        gap: 6rem;
        display: flex;
        flex-direction: column;
        position: relative;
        top: 36rem;
        left: -4rem;
        white-space: nowrap;
    }

    .r-main-card{
        top: -1rem;
        display: flex;
        flex-direction: column;
        width: 22rem;
        height: 25rem;
    }

    .Re-h1{
        position: absolute;
        top: 1rem;
        left: -1rem;
         font-size: 24px;
         display: flex;
         flex-direction: column;
    }

    .Re-h1 span{
        font-size: 24px;
        top: -1.5rem;
        left: 10.8rem;
    }

    .line{
        width: 5rem;
        padding: 1px 0;
        margin-top: 5rem;
         top: 8rem;
         left: 1rem;
         background-color: #fff;
         border: none
    }


  
    .ul-with{
        width: 20rem;
    }

    // .r-main-card {
    //     display: flex;
    //     flex-direction: column;
    //     margin-right: 2rem;
    // }
    .r-heading{ 
      gap: 5px;
       position: relative;
       top: 31.5rem;
       right: 4rem;
    }
    .r-span{
        position: relative;
        top: 10px;
    }
    .h-main-card{
        font-size: 18px;
        position: relative;
        left: -1rem;
    }
}

