@import "../../css-config/mixins.scss";

.cart-panel {
  position: fixed;
  top: 0;
  left: -2.6rem;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  z-index: 99;

  

  .opac-layer {
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    @include md{
      display: none;
    }
  }

  .cart-content {
    height: 41.5rem;
    width: 17.2rem;
    background: #43cea7;
    position: absolute;
    left: 8.9rem;
    z-index: 1;
    flex-direction: column;
    transform: translateX(100%);
    animation: slideCardWindow 0.3s ease forwards ;
    @include md {
      display: none;
      width: 340px;
      height: 100vh;
    }
    .cart-header{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 20px 15px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      .heading{
        flex-grow: 1;
        margin-bottom: 0;
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
      }
    
  
     
      .close-btn{
        display: flex;
        align-items: center;
        gap: 5px;
        cursor: pointer;
        svg{
          font-size: 18px;
        }
        .text{
          text-transform: uppercase;
          font-size: 14px;
        }
        &:hover{
          opacity: 0.5;
        }
      }
     
    }
    .empty-cart{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;
        margin-top: 100px;
        .cart-left{
          margin-top: -5rem;
        }
      .nave-link {
        padding: 10px 0;
        text-decoration: none;
        color: #2b2e3d;
        border-radius: 5px;
        text-align: center;
        width: 16rem;
        height: 2rem;
        font-weight: 700;
        font-size: 20px;
        display: flex;
        flex-direction: column;
        gap: 4rem;
      }
      
      &:hover{
        .nave-link{
          color: #43cea7;
        }
       
      }
    }
  }
}


@keyframes slideCardWindow {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}