
.shop-by-category {
    display: flex;
    position: absolute;
    top: 45rem;
    left: -5.5rem; 
}

.categorys {
    margin-top: 4rem;
    background: #daede8;
    border: 2px solid #43cea7;
    width: 34rem;
    height: 24rem;
    border-radius: 10px;
    cursor: pointer;
}

.categorys:hover{
    background: #fff;
}

.category-h1 {
    position: relative;
    left: 11rem;
    
}

.category-span {
    position: relative;
    left: 4rem;
    top: -2.4rem;
}

.Category-heading {
    margin-top: 1rem;
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.Category-heading h1 {
    position: relative;
    left: -1rem;
}

.Category-heading p {
    text-align: center;
    width: 12rem;
    position: relative;
    top: -17.5rem;
    left: 0.5;
}

.category-button{
   position: fixed;
   top: 2rem;
   left: 7rem;
    border: none;
font-weight: 700;
color: #43cea7;
}

.L-hr {
    position: relative;
    left: 13rem;
    width: 2px;
    height: 15rem;
    background-color: #43cea7;
}

.category-link {
    padding: 7px 10px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    left: 13rem;
    top: -15.5rem;
    background-color: #43cea7;
    color: #fff;
    cursor: pointer;
}

.category-img {
    position: relative;
    top: 8.5rem;
    left: -14.5rem;
}

.category-img img {
    border-radius: 10px;
    border: 2px solid gray;
    // filter: grayscale(100%);
    width: 13.5rem;
    height: 15em;
    cursor: pointer;
}

.card1 {
    position: relative;
    left: -10rem;
    cursor: pointer;
}

.card1 img {
    position: relative;
    top: -28rem;
    left: 31rem;
    cursor: pointer;
}

.card2 {
    position: relative;
    left: -75.6rem;
    top: 27rem;
    cursor: pointer;
}

.card2 img {
    position: relative;
    left: 31rem;
    top: -28rem;
    cursor: pointer;
}

.card3 {
    position: relative;
    left: -72rem;
    top: 27rem;
    cursor: pointer;
}

.card3 img {
    position: relative;
    left: 31rem;
    top: -28rem;
     cursor: pointer;
}

.card4 {
    position: relative;
    left: -103rem;
    top: 54rem;
    cursor: pointer;
}

.card4 img {
    position: relative;
    left: 31rem;
    top: -28rem;
    cursor: pointer;
}

.card5 {
    position: relative;
    left: -168.5em;
    top: 54rem;
    cursor: pointer;
}

.card5 img {
    position: relative;
    left: 31rem;
    top: -28rem;
    cursor: pointer;
}


/* Adjustments for mobile screens */
@media (max-width: 768px) {
    .shop-by-category {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: absolute;
        top: 58rem;
        left: auto;
        width: 100%;
    }

    .categorys {
        margin-top: 2rem;
        width: 90%;
        max-width: 100%;
        height: 29rem;
        left: 5%;
    }

    .category-h1 {
        text-align: center;
        left: -4.2rem;
        top: 1rem;
    }

    .category-button {
        display: none;
       width: 1rem;
       border: none;
       font-weight: 700;
       color: #43cea7;
       position: relative;
       top: -2rem;
     
    }

    .category-span {
        left: 0;
        top: 0;
    }

    .Category-heading {
        margin-top: 1rem;
        margin-left: 0;
    }

    .Category-heading h1 {
        text-align: center;
        font-size: 24px;
        left: 0;
    }

    .Category-heading p {
        text-align: center;
        width: 98%;
        top: 9.5rem;
        left: 0.5rem;
    }

    .L-hr {
        height: 2px;
        width: 20rem;
        left: 50%;
        top: 11rem;
        /* Center the line horizontally */
        transform: translateX(-50%);
        /* Center it exactly */
    }

    .category-link {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        top: -2rem;
    }

    .category-img {
        left: 5rem;
        top: -24.5rem;
    }

    .category-img img {
        position: relative;
        left: -5rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }

    .category-link {
        top: -20rem;
        left: 7rem;
    }

    .card1 {
        top: -12rem;
        left: -0.1rem;
        width: 90%;
        height: 29rem;
    }

    .card1 img {
        position: relative;
        left: -3rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }

    .card2 {
        top: -10rem;
        left: -0.1rem;
        width: 90%;
        height: 29rem;
    }

    .card2 img {
        position: relative;
        left: -3rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }

    .card3 {
        top: -8rem;
        left: -0.1rem;
        width: 90%;
        height: 29rem;
    }

    .card3 img {
        position: relative;
        left: -3rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }

    .card4 {
        top: -6rem;
        left: -0.1rem;
        width: 90%;
        height: 29rem;
    }

    .card4 img {
        position: relative;
        left: -3rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }

    .card5 {
        top: -4rem;
        left: -0.1rem;
        width: 90%;
        height: 29rem;
    }

    .card5 img {
        position: relative;
        left: -3rem;
        top: -1rem;
        width: 16rem;
        height: 11rem;
    }
}