@import "../../../css-config/mixins.scss";

.C-container{
    margin-top: 40rem ; 
    position: absolute;
    top: 80rem;
    left: 5rem;
    display: flex;
    flex-direction: column;
    gap: 10rem;

}
    .CTitle{
        position: relative;
        top: 15rem;
    }
    .C-wrapper{
        background-color: #43cea7;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
        position: relative;
        top: 7rem;
        height: 17rem;
        width: 85vw;
        display: flex;
        justify-content: space-between;
    }
        .C-left{
            padding: 2px 0;
        }
            .C2-heading{
                display: flex;
                flex-direction: column;
                margin-top: 3rem;
                margin-left: 35rem;
            }
            .C-heading{
                display: flex;
                flex-direction: column;
                margin-top: 3rem;
                margin-left: 1rem;
            }
            .h1-heading{
                color: #fff;
            }
            .C-para{
                width: 30rem;
            }
            .C-button2{
                position: absolute;
                left: 45rem;
                top: 13rem;
                border: none;
                padding: 8px 20px;
                border-radius: 20px;
                background-color: #fff;
            }
            .C-button{
                position: absolute;
                left: 8rem;
                top: 13rem;
                border: none;
                padding: 8px 20px;
                border-radius: 20px;
                background-color: #fff;
            }
                    .C-button:hover{
                        background-color: #9ed8c8;
                    }
                
            
         
            .C-image2{
                height: 22rem;
                width: 37rem;
                position: absolute;
                top: -4.5rem;
                left: -2.1rem;
               
            }
          
            .C-image{
                height: 33rem;
                width: 20rem;
                position: absolute;
                top: -8.5rem;
                left: 40rem;
            }
        
            @media screen and (max-width: 768px) {
                
                .C-container {
                    top: 197rem;
                    left: 1rem;
                    gap: 2rem;
                }

                .C2-heading{
                    position: relative;
                    top: -2.5rem;
                    right: 33rem;
                    font-size: 14px;
                }
                .C2-heading h1{
                    margin-left: 1rem;
                }
                .h1-heading{
                    margin-top: -2rem;
                    margin-left: 2rem;
                }
            
               .C-para{
                position: relative;
                top: 15rem;
                text-align: center;
                width: 20rem;
               }
                .C-wrapper {
                    width: 23.3rem; 
                    height: 32rem;
                    left: -1rem;
                   
                }
                .C-image{
                    width: 9rem;
                    height: 13rem;
                    position: relative;
                    left: 6.5rem;
                    top: -7.2rem; 
                }
                .C-image2{
                    width: 20rem;
                    height: 13rem;
                    position: relative;
                    left: 1.5rem;
                    top: -7.3rem;
                }
                 .C-button{
                    left: 8rem;
                    top: 29rem;
                 }
                .C-button2 {
                    left: 8rem;
                   top: 29rem;
                
                }
            }
   

