.jobs-container {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}


.main-jobs {
    // border: 2px solid #43cea7;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
    width: 80%;
    height: 15rem;
}

.card-title{
    width: 90%;
}



.jobs-header {
    margin-top: 1rem;
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.jobs-header .card-title {
    margin-top: -2rem;
}

.card-hr {
    width: 20rem;
}

h5 {
    width: 6rem;
    text-align: center;
    cursor: pointer;
    background-color: #e7715f;
    color: white;
    border-radius: 10px;
    position: relative;
    left: 50rem;
    top: -7rem;
}


.check {
    font-size: 1px;
    color: #43cea7;
}

.jobs {
    position: relative;
    left: 0;
}

.jobs-button {
    margin-left: 2rem;
    margin-top: -6rem;
}

.jobs-button {
    background-color: #43cea7;
    color: white;
    padding: 8px 20px;
    border-radius: 10px;
    border: none;
}

.job-skill {
    display: flex;
    gap: 0.2rem;
    position: relative;
    left: 24.5rem;
    top: -1.5rem;
}

.job-skill p {
    background-color: gray;
    color: white;
    width: 6rem;
    border-radius: 10px;
    text-align: center;
}



/* Media Query for Phones (adjust max-width as needed) */
@media (max-width: 768px) {
    .main-jobs {
        width: 92.5%;
        /* Make it full width for small screens */
        height: 18rem;
        /* Let the content determine the height */
    }

    .card-hr {
        width: 17rem;
        margin-left: 1rem;
    }

    h5 {
        font-style: italic;
        padding: -2px 3px;
        font-size: 13px;
        background: none;
        // border: 0.1px solid #43cea7;
        color: gray;
        width: 5rem;
        left: 15.5rem;
        top: -5.5rem;
    }

    .jobs-button {
        margin-left: 8rem;
        margin-top: 2rem;
    }

    .job-skill {
        padding: 0 4px;
        gap: 0rem;
        left: -0.6rem;
        top: -4rem;
    }

    .job-skill p {
        font-size: 10px;
        background: none;
        text-align: center;
        display: flex;
        align-items: center;
        padding: 0 15px;
        margin: 3px 2px;
        border:1px solid gray;
        color: gray;
        width: 3rem;
        height: 1rem;
        border-radius: 10px;
        text-align: center;
        position: relative;
        left: 5rem;
    }

    .jobs-header {
        margin-top: 1rem;
        margin-left: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    .jobs {
        font-size: 20px;
        left: 1rem;
    }
}