.privacy-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}
.privacy-text{
    font-size: 15px;
    font-weight: 100;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 90%;
}
.pri-heading{
    width: 80%;
}
.privacy-header{
    margin-top: 2rem;
    font-size: 25px;
}
.Digi-title{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-left: 7rem;
}

.privacy-content{
   margin-left: 7rem;
   margin-top: 2rem;
}
.privacy-content p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-content h1{
    position: relative;
}

.privacy-Info p{
  margin-top: 1rem;
}
.privacy-span{
   font-weight: 800;
}
.privacy-cont{
    margin-left: 6.5rem;
    margin-top: 1rem;
}
.privacy-cont p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-con{
    margin-left: 6rem;
    margin-top: 1rem;
}
.privacy-con p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-contE{
    margin-left: 6rem;
    margin-top: 1rem;
}
.privacy-contE p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-co{
    margin-left: 6rem;
    margin-top: 1rem;
}
.privacy-co p{
    width: 95%;
    margin-top: 1rem;
}

.privacy-c{
    margin-left: 6rem;
    margin-top: 1rem;
}
.privacy-c p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-Con{
    margin-left: 6rem;
    margin-top: 1rem;
}
.privacy-Con p{
    width: 95%;
    margin-top: 1rem;
}
.privacy-CON{
    margin-left: -10rem;
    margin-top: 1rem;
}
.privacy-CON p{
    width: 95%;
    margin-top: 1rem;
}

@media screen and (max-width: 768px) {
    .privacy-container {
        text-align: center;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 2rem;
    }
    .Digi-title{
        margin-left: 1.5rem;
    }
    .pri-heading{
        margin-left: 1.5rem;
    }
    .privacy-content p{
      position: relative;
      left: 2.5rem;
    }
  
    .privacy-content {
      margin: 0;
    }
  
    .privacy-content p {
      width: 20rem;
      margin-left: -1rem;
    }
    .privacy-header{
        margin-top: 2rem;
        font-size: 24px;
    }
  
    .privacy-cont,
    .privacy-con,
    .privacy-contE,
    .privacy-co,
    .privacy-c,
    .privacy-Con,
    .privacy-CON,
    .privacy-coN,
    .privacy-conteN,
    .privacy-contenn {
      margin: 0;
    }
    .privacy-cont,
    .privacy-con,
    .privacy-contE,
    .privacy-co,
    .privacy-c,
    .privacy-Con,
    .privacy-CON,
    .privacy-coN,
    .privacy-conteN,
     p{
      margin-left: -0.1rem;
      width: 20rem;
    }
    .privacy-cont p{
        margin-left: 1rem;
    }
    .privacy-contenn{
        margin: 0;
        top: -0.5rem;
    }
    .privacy-contenn p{
        margin-left: -1rem;
      width: 21rem;
    }
  }

