@import "../../../css-config/mixins.scss";
.Newsletter-section{
    // margin-top: 153rem;
    margin-top: auto;
    width: 100%;
    height: 350px;
    display: flex;
    align-items: center;
    background: #f9f9f9 url('../../../assets/newsletter-bg.jpeg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
     @include md{
        // margin-top: 228rem;
        margin-top: auto;
       
     }
    .Newsletter-content{
        display: table;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        text-align: center;
    }
    .small-text{
       margin-bottom: 15px;
       text-transform: uppercase;
       color: rgba(0, 0, 0, 0.5);
    }
    .big-text{
        margin-bottom: 20px;
        font-size: 22px;
        line-height: 30px;
        font-weight: 500;
        text-transform: uppercase;
        padding: 0 30px;
        @include md{
            font-size: 28px;
        }
    }
    .form{
        display: flex;
        gap: 5px;
        margin-bottom: 10px;
        .input{
            width: 200px;
            height: 40px;
            border-radius: 0;
            border: 1px solid rgba(0, 0, 0, 0.2);
            padding: 0 20px;
            font-size: 16px;
            outline: none;
            @include md{
                width: 300px;
            }
        }
        .n-btn{
            margin-top: 0.5rem;
            outline: 0;
            border: 0;
            height: 40px;
            width: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            font-size: 16px;
            color: white;
            background-color: #43cea7;
            border-bottom: 3px solid #43cea7;
            @include md{
                width: 120px;
            }
        }
    }
    .text{
        margin-bottom: 20px;
        font-size: 14px;
        color:rgba(0, 0, 0, 0.5) ;
    }
    .social-icons{
        display: flex;
        gap: 10px;
        .icon{
            width: 25px;
            height: 25px;
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 50%;
            color: white;
            display: flex;
            justify-content: center;
            align-items: center;
        }
      
    }
}





























