.ui-container{
    width: 100%;
    height: 15rem;
    background-image: url("ui.webp");
    background-repeat:no-repeat ;
    background-position: center;
    background-size: cover;
}



.s-image{
    width: 100%;
    height: 48rem;
}

.s-heading{
    position: relative;
    top: -20rem;
} 

.ui-heading{
   position: relative;
   left: 6.3rem;
   top: 6rem;
}

.bg-image{
    display: flex;
    gap: 2rem;
    position: relative;
    top: 8rem;
    left: 6rem;
}
.bg-image img{
    width: 20rem;
    height: 25rem;
    border-radius: 10px;
    border: 3px solid #43cea7;
}

@media (min-width: 768px){
    .ui-container {
        height: 100vh;
        // margin-left: -10;
        background-size: cover;
    }
  }