@import "../../css-config/mixins.scss";

.main-header {
  width: 100%;
  padding: 0 20px;
  background-color: #b6ecde;
  color: #2b2e3d;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  z-index: 99;
  @include md {
    padding: 0 40px;
  }

  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    max-width: 1200px;
    margin: 0 auto;
    @include md {
      height: 80px;
    }

    .left {
      list-style-type: none;
      display: none;
      gap: 25px;
      @include md {
        display: flex;
      }

      li {
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        text-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        cursor: pointer;
        &:hover{
          color: #43cea7;
          border-bottom: 3px solid #43cea7;
        }
      }
   
      .nave-link{
        color: #2b2e3d;
        text-decoration: none;
        &:hover{
          color: #43cea7;
        } 
      } 
   
    
    }
    .center {  
      font-weight: bold;
      text-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
      cursor: pointer;
      @include md {
        font-size: 34px;
        position: absolute;
        left: 90%;
        transform: translateX(-50%);
      }
      .center-img{
        width: 7rem;
        height: 5rem;
        margin-left: -1rem;
        @include md{
          width: 11rem;
          height: 7rem;
        }
      }
    }
     
    .menu-icon{
       position: relative;
      @include md{
        display: none;
      }
    }
    .right {
      display: flex;
      align-items: center;
      gap: 20px;

      @include md {
        gap: 25px;
      }

      svg {
        font-size: 20px;
        cursor: pointer;

        @include md {
          font-size: 24px;
        }
      }

      .cart-icon {
        position: relative;

        span {
          min-width: 20px;
          text-align: center;
          background-color: #8e2de2;
          padding: 2.5px;
          position: absolute;
          top: -5px;
          right: -12px;
          line-height: 1;
          border-radius: 10px;
        }
      }
    }
  }

  &.sticky-header {
    position: sticky;
    top: 0;
    transition: stickyHeader 0.4s ease forwards;
  }
}

@keyframes stickyHeader {
  0% {
    transform: translateY(-80px);
  }

  100% {
    transform: translateY(0);
  }
}

