

.c-app{
    background-image: url('saif.jpg');
    background-repeat: no-repeat;
    background-size: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 135vh;
}

form {
    width: 37%;
    position: absolute;
    left: 21rem;
    top: 6rem;
    padding: 0px 60px;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(26px) saturate(200%);
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: 12px;
    border: 1px solid rgba(209, 213, 219, 0.3);
}

.formInput{
    display: flex;
    flex-direction: column;
    width: 300px;
    border: 1px solid #43cea7;
    border-radius: 10px;
}

input{
    margin-top: 1rem;
    padding: 10px;
    margin: 7px 0;
    border-radius: 10px;
    border: 1px solid #43cea7;
}
.reg-snap{
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none;
}

label{
    font-size: 12px;
    color: #43cea7;
}

input:invalid[focused="true"] {
    border: ipx solid red;
}

input:invalid[focused="true"] ~ span{
    display: block;
}

.contact-p{
    margin-top: 12px;
    color: #43cea7;
    
}

