.con-h{
    margin-left: 2rem;
    margin-top: 1rem;
    font-size: 40px;
    color: black;
    text-align: center;
}

.con-button {
    width: 92.5%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: #43cea7;
    color: white;
    border-radius: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 30px;
}

.main-image {
    margin-left: 34rem;
    margin-top: -5rem;
}


/* Media query for screens up to 768px wide */
@media screen and (max-width: 768px) {
    .c-app {
        background-image: url('saif.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 115vh;
    }

    form {
        position: relative;
        left: 0rem;
        top: 0;
        width: 21rem;
        padding: 10px;
        backdrop-filter: none;
        -webkit-backdrop-filter: none;
        background-color: rgba(255, 255, 255, 1);
        border-radius: 10px;
        border: none;
    }

    .con-h {
        margin-left: 0;
        margin-top: 0;
        font-size: 24px;
        text-align: center;
    }

    .con-button {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .main-image {
        margin-left: 0;
        margin-top: 0;
    }
}
