.main-container{
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.j-container{
    background-color: #ceece4;
    box-sizing: border-box;
    height: 20rem;
}
.j-main{
    position: relative;
    top: 8rem;
    left: 6rem;
}
.j-header{
    margin-top: -2rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 30rem;
}
.j-header h1{
    font-size: 38px;
   
}
 p{
    width: 28rem;
}
.j-image img{
    position: relative;
    height: 20rem;
    top: -15rem;
    left: 33rem;
}

.Link-button{
    color: #43cea7;
    text-decoration: none;
}

.j-choose{
    // margin-top: rem;
    margin-left: 7rem;
}
.j-wrapper{
    margin-left: 7rem;
    margin-top: 1rem;
    border-radius: 10px;
    background-color: #43cea7;
    width: 80%;
    height: 19rem;
}
.j-title{
    position: relative;
    top: 4rem;
    left: 4rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.j-title p{
    width: 20rem;
}
.Search-btn{
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    width: 5.5rem;
    height: 2.3rem;
}
.title-img{
    position: relative;
    top: -14rem;
    left: 30rem;
    width: 20rem;
    height: 20rem;
}
.left-title{
    position: relative;
    top: 4rem;
    left: 28rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.j-para{
    width: 20rem;
}
.Search-button{
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    width: 5.5rem;
    height: 2.3rem;
}
.title-image{
    position: relative;
    top: -13.5rem;
    left: -26rem;
    width: 19rem;
    height: 19rem;
}
.title-img3{
    position: relative;
    top: -18rem;
    left: 30rem;
    width: 25rem;
    height: 25rem;
}

// media queries here 

@media screen and (max-width: 768px) {
    .j-main {
        top: 4rem;
        left: 1rem;
    }
    .j-container{
        height: auto;
    }

    .j-header {
        gap: 1rem;
        position: relative;
        top: 20rem;
        width: 100%;
    }

    .j-header h1 {
        font-size: 28px;
    }

    p {
        margin-left: 1rem;
        width: 20rem;
    }

    .j-image img {
        width: 25rem;
        height: 25rem;
        top: -15rem;
        left: -2rem;
    }

    .j-choose {
        margin-top: 1rem;
        margin-left: 1rem;
    }

    .j-wrapper {
        margin-left: 0;
        margin-top: 1rem;
        width: 100%;
        height: 30rem;
    }

    .j-title {
        top: 1rem;
        left: 0;
    }
    .j-title h1{
        font-size: 30px;
        margin-left: 1rem;
    }

    .j-title p {
        margin-left: 3rem;
        width: 18rem;
    }

    .title-img {
        top: -5rem;
        left: 2rem;
        width: 20rem;
        height: auto;
    }

    .left-title {
        top: 1rem;
        left: 1rem;
    }

    .left-title h1{
        margin-left: 1rem;
    }
    .span{
        position: relative;
        top: 8px;
    }
    .left-title p{
        margin-left: 3rem;
        width: 20rem;
    }

    .j-para {
        width: 100%;
    }

    
    .Search-btn {
        position: relative;
        top: 18rem;
        margin-left: 4rem;
        width: 15rem;
    }
    .Search-button{
        position: relative;
        top: 17rem;
        margin-left: 4rem;
        width: 15rem;
    }

    .title-image {
        top: -5rem;
        left: 2rem;
        width: 18rem;
        height: auto;
    }

    .title-img3 {
        top: -8rem;
        left: 0;
        width: 100%;
        height: auto;
    }
}