@import "../../css-config/mixins.scss";

.s-container{
    display: flex;
    box-sizing: border-box;
    .s-image{
        margin: 1px 0;
    }
    img{
        width: 100%;
        max-height: 90%;
        @include md{
            width: 100%;
            height: 75%;
        }
    }
    .s-heading{
        position: absolute; 
        top: 2rem;
        left: -2.3rem;
        display: flex;
        gap: 1rem;
        flex-direction: column;
        @include md{
            gap: 2rem;
            top: 10rem;
            left: 8rem;
        }
    }
     h1{
        margin-left: 1rem;
        font-size: 30px;
        @include md{
            font-size: 60px;
        }
    }
    .s-para{
        width: 70%;
        @include md{
           width: 40%;
        }
    }
}

.s-overview{
    background: cover;
    margin-left: 5rem;
    width: 85%;
    display: flex;
    gap: 2rem;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 19rem;
    left: -2.4rem;
    @include md{
     top: 46rem;
     left: 2rem;
    }
    .s-heading{
        font-size: 30px;
        gap: 15px;
        flex-direction: row;
        font-size: 30px;
        position: absolute;
        top: -1rem;
        left: -3rem;
    }
    .line{
        top: 1rem;
        width: 35%;
        margin-left: -3rem;
        padding: 1px 0;
        background-color: #43cea7;
    }
    .s-Text{
        margin-left: -1rem;
        width: 20rem;
        @include md{
            width: 90%;
        }
    }
   
}

.sin-card{
    position: relative;
    top: -27rem;
    @include md {
        top: -3rem;
    }
}

.language-icon{
    position: relative;
    top: -33rem;
    @include md{
        top: 3rem;
    }
}

