.about-image{
    width: 100%;
    height: 50vh;
    filter:contrast(15%);

}
.about-h1{
    position: relative;
    top: -10rem;
    left: 33rem;
    color: white;
}
.about-title{
    margin-top: 3rem;
    margin-left: 4rem;
    
}
.about-title2{
  position: relative;
  top: -6rem;
  left: 25rem;

}
.about-title2 p{
    width: 60%;
}
.about-title p{
    width: 60%;
}
.left-img{
   position: relative;
   left: 45rem;
   top: -14rem;
  width: 20rem;
  height: 20rem;
}
.left-img2{
   position: relative;
   left: -22rem;
   top: -14rem;
  width: 20rem;
  height: 20rem;
}

/* Media query for smaller screens, */
@media (max-width: 768px) {
    .about-h1 {
        top: -13rem;
        left: 8rem;
    }

    .about-title{
        margin-top: -1rem;
        margin-left: 2rem;
    }
    .about-title p{
        margin-left: -0.5rem;
        width: 21rem;
    }
    .about-title h1{
        margin-left: -7px;
    }
    .about-title2 {
       position: relative;
       top: 1rem;
       margin-left: -24rem;
    }
    .about-title2 p{
        margin-left: -1px;
        width: 21rem;
    }

    .left-img {
        /* Adjust positioning and size for smaller screens */
        position: relative;
        left: 0;
        top: 0;
        width: 20rem;
        height: auto;
    }
    .left-img2{
        position: relative;
        left: 0;
        top: 0;
        width: 20rem;
        height: auto;
    }
}