.IAT-container{
    width: 100%;
    height: 30vh;
    background-image: url("IOT.webp");
    background-repeat:no-repeat ;
    background-position: center;
    background-size: cover;
  }

  @media (min-width: 768px){
    .IAT-container {
      height: 100vh;
      background-repeat:no-repeat ;
      background-position: center;
      background-size: cover;
    }
  }
  