@import "../../css-config/mixins.scss";
.products-container{
    padding: 3rem 0;
     position: absolute;
     top: 381.5rem;
    margin: 50px 0px;
    overflow-x: hidden;
    @include md{
        top: 233rem;
        margin: 75px 65px;
    }
    .sec-Heading{
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: bold;
        text-transform: uppercase;
        @include md{
            margin-bottom: 35px;
            font-size: 24px;
        }
        &:after{
            content: "";
            display: block;
            margin-top: 5px;
            width: 50px;
            height: 3px;
            background-color: #43cea7;
            @include md{
                margin-top: 10px;
            }

        }
    }
    .products{
        width: 22rem;
        display: flex;
        // overflow-x: scroll;
        gap: 10px;
        @include md{
            width: 68rem;
            gap: 20px;
        }
    }
    // .products::-webkit-scrollbar{
    //    scrollbar-color:#fff;
    //     width: 0;
    //   }
    .p-main{
        // box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
        background: none;
        display: flex;
        width: 25rem;
        height: 18rem;
        border-radius: 15px;
        @include md{
            width: 30rem;
        }
    }
    .p-title{
       margin-left: 5rem;
      gap: 1rem;
    }
    .p-title h2{
        color: #43cea7;
    }
    img{
        background: #43cea7;
        border-radius: 50%;
        background-size: 3rem;
        padding: 5px 5px;
        position: relative;
        left: -3rem;
        top: 1rem;
        width: 3.5rem;
        height: 3.5rem;
    }
    .p-heading{
     position: relative;
     left: 2rem;
     top: -2.5rem;
    }
    .star{
        position: relative;
        left: -2.5rem;
        top: 6rem;
    }
    .p-desc{
        position: relative;
        top: -3rem;
        left: -2rem;
    }
    .p-text{
        width: 17.5rem;
        @include md{
            width: 19rem;
        }
    }
}

@media (max-width: 768px) {
    .sec-Heading {
      font-size: 1.5rem;
    }

    .p-main {

      width: 100%;
    }

    .p-title {
      font-size: 1rem;
    }

    .p-img {
         width: 50px;
      height: 50px;
    }

    .p-heading {
    
      font-size: 1.2rem;
    }

    .star {
    top: -4rem;
    }

    .p-text {
    
      font-size: 0.9rem;
    }
  }




















