
.languade-container{
    position: relative;
    left: 5.3rem;
    top: 15rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 30rem 0;
}
.LanguageIcon{
    position: relative;
    top: -23rem;
}
.L-main-card{
    border: 2px solid #43cea7;
    border-radius: 10px;
    margin-top: -4rem;
    width: 21rem;
    height: 23rem;
}
.h3-main-card{
    margin-top: 1rem;
 text-align: center;
}

.web-card{
    flex-wrap: normal;
    white-space: wrap;
    box-sizing: border-box;
}

.L-heading{
   flex-wrap: wrap;
   position: relative;
   left: -3.5rem;
   top: 57rem;
}
.L-heading-textt{
//   position: absolute;
//   top: -50rem;
//   width: 30rem;
}
.L-span{
   position: absolute;
   margin-top: 0.1rem;
   margin-left: 0.5rem;
}
.L-line{
    display: flex;
    width: 16rem;
    background-color: #43cea7;
    padding: 1px 0;
    margin-top: 1rem;
    margin-left: 1rem;
}
.L-img{
    border-radius: 10px;
    position: relative;
    left: -0.1rem;
    width: 3.5rem;
    height: 2.5rem;
}
.l-but{
    background-color: #43cea7;
    border: none;
    border-radius: 10px;
    position: relative;
    top: 1rem;
    left: 5.5rem;
    padding:9px 0;
}




@media (min-width: 768px){
    .languade-container{
        flex-direction: row;
        margin-left: -9rem;
        padding: 20rem 0;
    }
    .LanguageIcon{
        top: -8rem;
        left: -2rem;
    }
   .L-heading{
      top: 10rem;
      left: 14rem;
    }
    .L-heading-text{
     
        font-size: 34px;
        margin-top: -22rem;
        margin-left: -5rem;
    }
    .h3-main-card{
    margin-top: -1.5rem;
}

    .web-card {
        flex-wrap: normal;
        white-space: wrap;
        box-sizing: border-box;
     }
    .L-span{
        margin-top: -27rem;
        top: 5.9rem;
    }
    .L-main-card{
        padding: 40px 0;
        width: 20.5rem;
        height: 24rem;
       
    }
    .l-but{
        background-color: #43cea7;
        border: none;
        border-radius: 10px;
       top: 0.1rem;
       left: 4.5rem;
        padding:7px 16px;
    }
    .L-line{
        margin-left: -4.5rem;
    }
    .L-img{
        border-radius: 10px;
        padding: 1px 3px;
        gap: 1rem;
        position: relative;
        left: -0.1rem;
        width: 3.5rem;
        height: 2.5rem;
    }
   
}