
.Cloud-container{
    width: 100%;
    height: 100vh;
    background-image: url("cloud.webp");
    background-repeat:no-repeat ;
    background-position: center;
    background-size: cover;
}

.ai-header{
    position: relative;
    left: -40rem;
    top: 13rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cloud-heading{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;
     left: 35rem;
     top: -30rem;
}
.s-image{
    width: 100%;
}

.s-Text{
    width: 90%;
    margin-left: 2rem;
}
.cloud-para{
    width: 30rem;
}

.s-heading{
    margin-left: 3rem;
    margin-top: 4rem;
}
.line{
    margin-left: 3rem;
    margin-top: 4rem;
}

.Cloud-language{
   position: relative;
   top: 10rem;
}

.cloud-wrapper{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

.cloud-Card{
    gap: 2rem;
    width: 20rem;
    height: 25rem;
    border-radius: 10px;
    background-color: #cbebe2;
    border: 2px solid #43cea7;
    position: relative;
    top: 7rem;
    left: 6rem;
}
.cloud-details{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-top: 1rem;
    text-align: center;
}

.cloud-details img{
    width: 10rem;
    height: 10rem;
    margin-top: -1.5rem;
    margin-left: 5rem;
}
.cloud-details p{
    width: 20rem;
    margin-top: -1rem;
}

.cloud-language{
    position: relative;
    top: -9rem;
}

@media (max-width: 768px) {
    .Cloud-container {
        height: 15rem;
        // margin-left: -10;
        background-size: cover;
    }
    
    .cloud-heading {
        gap: 15rem;
        left: -2rem;
    }
    .cloud-language{
        top: -38rem;
    }

    .s-image{
        width: 100%;
    }

    .cloud-Card {
        display: flex;
        flex-direction: row;
        width: 100%;
        height: auto;
        left: 3rem;
        top: 13rem;
    }
}






