.Ai-container{
    width: 100%;
height: 100vh;
    background-image: url("Ai.webp");
    background-repeat:no-repeat ;
    background-position: center;
    background-size: cover;
}
.ai-wrapper{
   position: relative;
   left: 38rem;
   top: 5rem;
}
.cloud-Card{
    width: 20rem;
    position: relative;
    left: 5.5rem;
}
.ai-header{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.ai-para{
    width: 90%;
}
.ai-header2{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
    top: 4rem;
    left: 6rem;
}

.AI-card{
    display: flex;
    flex-direction: row;
    gap: 2rem;
    position: relative;
    top: -7rem;
    padding: 10rem 0;
}

.ai-text{
    width: 83%;
}

/* Media query for screens smaller than 768px */
@media (max-width: 768px) {
    .Ai-container {
        height: 15rem;
        // margin-left: -10;
        background-size: cover;
    }
    .cloud-Card{
        width: 22rem;
        height: 26rem;
        margin-left: -0.5rem;
    }

    .ai-wrapper {
        left: 2rem;
        top: 2rem;
    }

    .ai-header2 {
        top: 2rem;
        left: 1.5rem;
    }
    .ai-hed{
        margin-left: 1rem;
    }
    .ai-header2{
        margin-left: -1rem;
    }
    .ai-para{
        text-align: center;
        width: 20rem;
    }
    .AI-card{
       flex-direction: column;
       margin-left: -4.2rem;
       top: -12rem;
    }

}


